@import './colors.scss';

/* Page transition */
#page-transition {
	position: relative;
	display: block;
	// z-index: 99999;
	z-index: 800;
}
.overlay {
	position: fixed;
	display: block;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: whitesmoke;
	z-index: 1;
}
.preloader__transition {
	position: fixed;
	top: 50%;
	left: 50%;
    text-align: center;
	text-transform: uppercase;
	font-size: 28px;
	font-weight: bold;
	color: rgb(87, 87, 87);
	z-index: 2;
	transform: translate(-50%, -50%);
}